<template>
  <div>
    <wh-pie :chartData.sync="chartData" width="443px" height="278px"></wh-pie>
  </div>
</template>
<script>
export default {
  name: 'WhPieChart',
  data() {
    return {
      chartData: [],
    }
  },
  mounted() {
    setTimeout(() => {
      this.chartData =  [{
        name: '测试1',
        value: 1
      }, {
        name: '测试2',
        value: 2
      }, ]
    }, 2000)
  },
  methods: {
   
  }
}
</script>
<style scoped>
 
</style>



